@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://use.typekit.net/twf2zjd.css);
html,
body,
#root {
	margin: 0;
	padding: 0;
	/* overflow-x: hidden; */
}
@font-face {
	font-family: 'Uni Sans Demo';
	src: local('Uni Sans Demo'),
		url(/equinox-final-react/static/media/Uni_Sans_Heavy.d97dd27f.ttf) format('truetype');
}

::-webkit-scrollbar {
	display: none;
}
* {
}
.mainDiv {
	height: 700vh;
	width: 100vw;
	background: linear-gradient(
		178.33deg,
		#8415aa -0.79%,
		#3d0678 6.05%,
		#100259 12.42%,
		#150864 18.69%,
		#1a1d85 23.78%,
		#2034a9 29.99%,
		#1662d8 36.6%,
		#1f91e4 43.09%,
		#26b1ed 47.72%,
		#71c8ed 52.2%,
		#94d9f6 56.19%,
		#99e0ff 63.55%,
		#6bc9f1 69.44%,
		#77b6e7 73.9%,
		#8895e7 79.37%,
		#9d77d5 84.48%,
		#a938c6 89.96%,
		#8415aa 95.04%,
		#3d0678 98.43%
	);
	background-blend-mode: multiply;
	z-index: 100000;
}

.page1bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Page1Content {
	width: 50vw;
	height: 60vh;
	background: linear-gradient(184.26deg, #610e91 3.47%, #120352 96.53%);
	box-shadow: 3px 1px 38px -12px #000000;
	border-radius: 28px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeIn {
	opacity: 1;
	transform: scale(1);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeOut {
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.left,
.right {
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.equinoxlogo {
	width: 80%;
	/* height: 245px; */
	-webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.dates {
	font-family: 'Uni Sans Demo';
	font-style: normal;
	font-weight: normal;
	font-size: 2vw;
	line-height: 19px;
	text-align: right;
	margin-top: 0;
	color: #ffffff;
}
.timer {
	font-family: 'Uni Sans Demo';
	font-style: normal;
	font-weight: normal;
	font-size: 4vw;
	line-height: 42px;
	text-align: right;
	color: #ffffff;
	margin-top: 0;
}

#devfolio-apply-now {
	align-items: center;
	outline: none;
	background-color: #3770ff;
	border-radius: 3px;
	border: none;
	color: #ffffff;
	cursor: pointer;
	display: flex;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
	font-size: 18px;
	font-weight: 600;
	padding: 12px 20px;
	text-align: center;
	text-transform: lowercase;
	cursor: pointer;
}

#devfolio-apply-now:not([disabled]) {
	cursor: pointer;
}

#devfolio-apply-now:hover {
	background-color: #2954bf !important;
}
.links {
	display: flex;
	margin-top: 5%;
	width: 48%;
	align-items: center;
	justify-content: space-between;
}
.linklogos {
	margin: 2%;
	width: 16%;
	cursor: pointer;
}
.at {
	width: 13%;
}
.linklogos:hover {
	transform: scale(1.1);
	transition: all 0.5s;
}

@media screen and (max-width: 1030px) {
	.Page1Content {
		width: 80vw;
		height: 80vh;
		flex-direction: column;
		opacity: 1;
	}
}

@media screen and (max-width: 720px) {
	.Page1Content {
		width: 80vw;
		height: 80vh;
		flex-direction: column;
		opacity: 1;
	}
	.equinoxlogo {
		width: 65%;
	}
	.left,
	.right {
		width: 100%;
		height: 50%;
	}
	.left {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.dates {
		font-size: 6vw;
	}
	.timer {
		font-size: 9vw;
		margin-bottom: 2%;
	}
	.linklogos {
		width: 16%;
	}
	.at {
		width: 14%;
	}
	.links {
		width: 70%;
	}
}

.page2bg {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
}
.Page2Content {
	width: 70vw;
	height: 80vh;

	background: linear-gradient(184.26deg, #14065f 3.47%, #1a2282 96.53%);
	box-shadow: 3px 1px 38px -12px #000000;
	border-radius: 28px;
	opacity: 1;

	justify-content: center;
	align-items: center;
	z-index: 1000;
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.Page2Content h1 {
	font-family: Uni Sans Demo;
	font-style: normal;
	font-weight: normal;
	font-size: 6vw;
	line-height: 1vw;
	text-align: center;

	color: #ffffff;
}
.fadeIn {
	opacity: 1;
	transform: scale(1);
	transition: all 500ms;
	transition: all 500ms;
}
.fadeOut {
	opacity: 0;
	transform: scale(0);
	transition: all 500ms;
	transition: all 500ms;
}
.aboutStuff {
	width: 80%;
	margin: auto;
	overflow-y: scroll;
	position: relative;
}
.aboutCont {
	margin-top: 0;
	font-family: 'acumin-pro';
	font-style: normal;
	font-weight: 500;
	font-size: 1.5vw;
	line-height: 1.8vw;
	text-align: justify;

	color: #ffffff;
}
@media screen and (max-width: 720px) {
	.Page2Content {
		width: 90vw;
		height: auto;
		max-height: 80vh;
		flex-direction: column;
	}
	.aboutCont {
		font-size: 100%;
		text-align: justify;
		line-height: 100%;
	}
	.aboutStuff {
		width: 90%;
		padding-bottom: 10%;
	}
	.Page2Content h1 {
		font-size: 9vw;
		line-height: 9vw;
		font-weight: normal;
	}
}
@media screen and (max-width: 600px) {
	.aboutCont {
		font-size: 80%;
		text-align: justify;
		line-height: 120%;
	}
}

